.header{
    display: flex;
    justify-content: space-between;
    margin-bottom: 30px;

}
.header__left{
    background-color: white;
    color: grey;
    border-radius: 30px;
    display:flex;
    align-items: center;
    padding: 10px;
    flex: 0.5;
    min-width: 80px;

}
.header__left > input {
    border: none;
    width: 100%;

}
.header__right{
    display: flex;
    align-items: center;
}
.header__right > h4 {
    margin-left:10px;

}